<template>
  <el-row>
    <el-col>
      <el-card>
        <div class="markdown">
          <h1>IconFont å¾æ  <span style="font-size: 16px">ç¹å»å¾æ å¤å¶ä»£ç å°ç²è´´æ¿</span></h1>
          <div>
            <el-switch v-model="isLabel" active-text="å¤å¶æ ç­¾" inactive-text="å¤å¶ç±»å">
            </el-switch>
          </div>
          <div class="icon_lists">
            <div>
              <h3>å¤è²å¾æ </h3>
            </div>
            <el-row>
              <el-col>
                <span class="icon-box" @click="clip(item,symbol)" v-for="(item) in symbol" :key="item.name">
                      <v-icon :name="item.name" symbol></v-icon>
                    </span>
              </el-col>
            </el-row>
            <div>
              <h3>å­ä½å¾æ </h3>
            </div>
            <el-row>
              <el-col>
                <span class="icon-box" @click="clip(item,null)" v-for="(item) in iconfont" :key="item.name">
                      <v-icon :name="item.name"></v-icon>
                    </span>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>
<script>
import Clipboard from 'clipboard'
import iconfont from '@/assets/iconfont/icon.js'
import symbol from '@/assets/iconfont/symbol-icon.js'
export default {
  name: 'iconfont',
  components: {},
  data() {
    return {
      iconfont: iconfont,
      symbol: symbol,
      isLabel: true
    }
  },
  watch: {},
  computed: {},
  methods: {
    clip(type, symbol) {
      let icon
      if (this.isLabel) {
        if (symbol) {
          icon = `<v-icon name="${type.name}" symbol></v-icon>`
        } else {
          icon = `<v-icon name="${type.name}" ></v-icon>`
        }
      } else {
        icon = type.name
      }
      console.log(icon)
      const clipboard = new Clipboard('.icon', {
        text() {
          return icon
        }
      })
      clipboard.on('success', (e) => {
        e.clearSelection()
        clipboard.destroy()
        this.$message({
          message: 'ä»£ç å·²ç»å¤å¶å°åªè´´æ¿',
          type: 'success'
        })
      })
    }
  },
  created() { },
  mounted() { }
}
</script>
<style>
.icon_lists li {
  font-size: 24px;
  cursor: pointer;
}
.icon_lists .iconfont {
  font-size: 24px !important;
}
.icon-box {
  display: inline-block;
  padding: 10px;
}
</style>



